.h-Flamenco span, .h-Flamenco-1, .h-White, .dashed, .solid{
    transition: all .3s ease-out;
    &:hover{
        transition: all .3s ease-out;
    }
}
.h-Flamenco{
    &:hover{
        span:not(.popup-desc){
            color: var(--Flamenco);
        }
    }
}
.h-Flamenco-1{
    &:hover{
        color: var(--Flamenco);
    }
}
.h-White{
    &:hover{
        color: var(--White);
    }
}
.h-Wedgewood{
    &:hover{
        color: var(--Wedgewood);
    }
}
.dashed, .solid{
    color: var(--Charcoal);
    display: inline-block;
    &:hover{
        color: var(--Wedgewood);
    }
}
.dashed{
    border-bottom: 1px dashed currentColor;
}